import { Link } from "react-router-dom";
import styled from "styled-components";
import { BsInstagram, BsFacebook, BsGithub } from "react-icons/bs";

const FooterWrapper = styled.footer`
background: #333;
color: #fff;
`
const FooterTop = styled.div`
border-bottom: 1px solid #555;
`
const FooterBottom = styled.div`
height: 80px;
`
const FInner = styled.div`
display: flex;
justify-content: space-between;
width: 1200px;
margin: 0 auto;
`
const UL = styled.ul`
display: flex;
gap: 30px;
align-items: center;
height: 80px;
`

const LI = styled.li`
position: relative;

&~&::after {
    content: '';
    position: absolute;
    inset: 50% auto auto -15px;
    transform:translate(0,-50%);
    width: 1px;
    height: 12px;
    background: #bbb;

}
`
const SnsWrapper = styled.div`
display: flex;
gap: 10px;
align-items: center;
height: 80px;
font-size: 24px;
`

const Address = styled.address`
display: flex;
align-items: center;
height: 80px;
font-style: normal;
`

const FamilyLinkWrapper = styled.div`
display: flex;
align-items: center;
height: 80px;
`

const Select = styled.select`
background: #333;
`

const FooterService = ({ DEFAULT }) => {
    return (
        <UL>
            {
                DEFAULT.service.map((service, idx) => {
                    return (
                        <LI key={idx}><Link to={service.link}>{service.title}</Link></LI>
                    )
                    // 짧을때는 return 생략가능 {} 안쓰고 => <li>{service.title}</li> 이거만
                })
            }
        </UL>
    )
}

const FooterSns = () => {
    return (
        <SnsWrapper>
            <a href="/" target="_blank"><BsInstagram /></a>
            <a href="/" target="_blank"><BsFacebook /></a>
            <a href="/" target="_blank"><BsGithub /></a>
        </SnsWrapper>
    )
}

const FooterAddress = ({ DEFAULT }) => {
    return (
        <Address>
            주소 : {DEFAULT.address} 전화 : {DEFAULT.tel}
        </Address>
    )
}

const linkOpen = e => {
    let link = e.target.value;
    link && window.open(link)
}
// 링크가 있으면 링크를 띄어라 &&는 둘다 값이 참이면 실행 familyLink 링크 안걸리게 하기위해서

const FooterLink = ({ DEFAULT }) => {
    return (
        <FamilyLinkWrapper>
            <Select onChange={linkOpen}>
                {
                    DEFAULT.familyLink.map((it, idx) => {
                        return (
                            <option value={it.link} key={idx}>{it.title}</option>
                        )
                    })
                }

            </Select>
        </FamilyLinkWrapper>
    )
}

const Footer = ({ DEFAULT }) => {
    return (
        <FooterWrapper>
            <FooterTop>
                <FInner>
                    <FooterService DEFAULT={DEFAULT} />
                    <FooterSns />
                </FInner>
            </FooterTop>
            <FooterBottom>
                <FInner>
                    <FooterAddress DEFAULT={DEFAULT} />
                    <FooterLink DEFAULT={DEFAULT} />
                </FInner>
            </FooterBottom>
        </FooterWrapper>
    )
}

export default Footer;